
#fullScreen {
  position: absolute;
  top: 50%;
  left: 50%;
  transform:translate(-50%, -50%);
}
.content {
  position: absolute;
  top: 70%;
  left: 50%;
  transform:translate(-50%, 0);
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
input {
  text-align: center;
}



